import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch, useHistory } from 'react-router-dom';
import Plotly from 'plotly.js-dist';
import LandingPage from './components/LandingPage';
import InflationData from './components/InflationData';
import ScatterPlot from './components/ScatterPlot';

function App() {
    const [scatterDataLoaded, setScatterDataLoaded] = useState(false);
    const [inflationData, setInflationData] = useState(null);
    const [r2Data, setR2Data] = useState(null);
    const [liveUpdate, setLiveUpdate] = useState(false);
    const [draftId, setDraftId] = useState("");

    const fetchInflationData = async () => {
        if (!draftId) {
            console.error("Draft ID is required");
            return;
        }
        try {
            const csrfTokenMeta = document.querySelector('meta[name="csrf-token"]');
            if (!csrfTokenMeta) {
                console.error("CSRF token not found");
                return;
            }
            const csrfToken = csrfTokenMeta.getAttribute('content');
            const response = await fetch('/inflation', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Accept': 'application/json',
                    'X-Requested-With': 'XMLHttpRequest',
                    'X-CSRF-TOKEN': csrfToken
                },
                body: `draft_id=${draftId}`
            });
            if (!response.ok) {
                throw new Error(`Error: ${response.statusText}`);
            }
            const data = await response.json();
            setInflationData(data);
        } catch (error) {
            console.error('Error fetching inflation data:', error);
        }
    };

    const fetchScatterData = async () => {
        if (!draftId) {
            console.error("Draft ID is required");
            return;
        }
        try {
            const response = await fetch(`/scatter_data?draft_id=${draftId}`);
            if (!response.ok) {
                throw new Error(`Server responded with a status: ${response.status}`);
            }
            const text = await response.text();  // Get raw response text for logging
            try {
                const data = JSON.parse(text);  // Attempt to parse JSON
                if (data && data.scatterplot) {
                    const trace = {
                        x: data.scatterplot.pick_no,
                        y: data.scatterplot.metadata_amount,
                        mode: 'markers',
                        marker: {
                            color: data.scatterplot.colors,
                            size: 10
                        },
                        text: data.scatterplot.player_names
                    };
                    const layout = {
                        xaxis: { title: 'Pick Number' },
                        yaxis: { title: 'Amount' }
                    };
                    Plotly.react('scatterPlot', [trace], layout);
                    setScatterDataLoaded(true);
                    setR2Data(data.r2_values); // Store R^2 data
                }
            } catch (jsonError) {
                console.error('Error parsing JSON:', jsonError);
                console.error('Response text:', text);
            }
        } catch (error) {
            console.error('Error fetching scatter data:', error);
        }
    };

    useEffect(() => {
        if (liveUpdate) {
            const interval = setInterval(() => {
                fetchInflationData();
                fetchScatterData();
            }, 2000);
            return () => clearInterval(interval);
        }
    }, [liveUpdate, draftId]);

    const handleToggleSwitch = () => {
        setLiveUpdate(!liveUpdate);
    };

    const handleDraftIdChange = (e) => {
        setDraftId(e.target.value);
    };

    return (
        <Router>
            <div>
                <div style={{ margin: '1rem 0' }}>
                    <label>Live Update:</label>
                    <input id="liveUpdateSwitch" onChange={handleToggleSwitch} type="checkbox" />
                </div>
                <form>
                    <label htmlFor="draft_id">Enter Draft ID:</label>
                    <input id="draft_id" name="draft_id" type="text" value={draftId} onChange={handleDraftIdChange} />
                </form>
                <button onClick={fetchScatterData}>Load Data</button>

                <div className="tab">
                    <button className="tablinks" onClick={() => setScatterDataLoaded(false)}>Inflation Data</button>
                    <button className="tablinks" onClick={fetchScatterData}>Picks Over Time</button>
                </div>

                <Switch>
                    <Route exact path="/" component={LandingPage} />
                    <Route path="/inflationdata" render={() => (
                        <InflationData draftId={draftId} liveUpdate={liveUpdate} />
                    )} />
                    <Route path="/scatterplot" render={() => (
                        <ScatterPlot draftId={draftId} scatterDataLoaded={scatterDataLoaded} r2Data={r2Data} />
                    )} />
                </Switch>
            </div>
        </Router>
    );
}

export default App;